import React, { useState, useEffect, useRef } from 'react';
import { Container, Typography, Button, Box, Card, CardContent } from '@mui/material';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import 'toastr/build/toastr.min.css';
import toastr from 'toastr';
import Recorder from 'recorder-js';

// Material-UI
const theme = createTheme();

toastr.options = {
    positionClass: 'toast-center-center', // 
    showDuration: 300,
    hideDuration: 300,
    timeOut: 2000,
    extendedTimeOut: 1000,
    showEasing: 'swing',
    hideEasing: 'linear',
    showMethod: 'fadeIn',
    hideMethod: 'fadeOut',
    preventDuplicates: true,
};

function App() {
    const [audioUrl, setAudioUrl] = useState('');
    const [uploadedFileName, setUploadedFileName] = useState('');
    const [jsonData, setJsonData] = useState(null); // null
    const [isRecording, setIsRecording] = useState(false);
    const recorder = useRef(null);
    const audioContext = useRef(null);
    const mediaStream = useRef(null);  // MediaStream

    // 
    useEffect(() => {
        //  AudioContext
        audioContext.current = new (window.AudioContext || window.webkitAudioContext)();
    }, []);

    // 
    const handleFileChange = async (event) => {
        const selectedFile = event.target.files[0];
        if (!selectedFile) return;
    
        const formData = new FormData();
        formData.append('file', selectedFile);
    
        try {
            const response = await fetch('http://localhost:5000/upload', {
                method: 'POST',
                body: formData,
            });
    
            if (response.ok) {
                const data = await response.json();
                const serverFileUrl = `http://localhost:5000/uploads/${data.filename}`;
                setAudioUrl(serverFileUrl);
                setUploadedFileName(data.filename);
    
                // 
                event.target.value = '';
    
                // 
                toastr.success('File uploaded successfully!');
            } else {
                const errorData = await response.json();
                toastr.error(`File upload failed: ${errorData.error}`);
            }
        } catch (error) {
            console.error('Error uploading file:', error);
            toastr.error('Error uploading file');
        }
    };
    

    // 
    const handleRecord = async () => {
        //  AudioContext
        if (audioContext.current.state === 'suspended') {
            await audioContext.current.resume();
        }

        if (isRecording) {
            recorder.current.stop().then(({ blob }) => {
                const audioUrl = URL.createObjectURL(blob);
                setAudioUrl(audioUrl);

                const formData = new FormData();
                const fileName = 'recording.wav';  // 
                formData.append('file', blob, fileName);
                setUploadedFileName(fileName);  // 

                fetch('http://localhost:5000/upload', {  // URL
                    method: 'POST',
                    body: formData,
                }).then(response => {
                    if (response.ok) {
                        toastr.success('Recording uploaded successfully!');
                    } else {
                        response.json().then(errorData => {
                            toastr.error(`Recording upload failed: ${errorData.error}`);
                        });
                    }
                }).catch(error => {
                    console.error('Error uploading recording:', error);
                    toastr.error('Error uploading recording');
                });
            });

            // MediaStream
            mediaStream.current.getTracks().forEach(track => track.stop());
            mediaStream.current = null;
            recorder.current = null;
            setIsRecording(false);
        } else {
            try {
                const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
                mediaStream.current = stream;
                recorder.current = new Recorder(audioContext.current, {
                    onAnalysed: data => console.log(data)
                });
                recorder.current.init(stream);
                recorder.current.start().then(() => setIsRecording(true));
            } catch (error) {
                console.error('Error accessing microphone:', error);
                toastr.error('Could not start recording');
            }
        }
    };

    // JSON
    const fetchJsonData = async () => {
        try {
            const response = await fetch('http://localhost:5000/results');  // URL
            if (response.ok) {
                const data = await response.json();
                setJsonData(data);
            } else {
                toastr.error('Failed to fetch JSON data');
            }
        } catch (error) {
            console.error('Error fetching JSON data:', error);
            toastr.error('Error fetching JSON data');
        }
    };

    // 
    const handleRecognize = async () => {
        try {
            // 
            await fetch('http://localhost:5000/recognize', {
                method: 'POST',
                body: JSON.stringify({ filename: uploadedFileName }),
                headers: {
                    'Content-Type': 'application/json'
                }
            });

            // JSON
            fetchJsonData();
        } catch (error) {
            console.error('Error during recognition:', error);
            toastr.error('Error during recognition process');
        }
    };

    // Overall Language
    const calculateOverallLanguage = () => {
        if (!jsonData || jsonData.length <= 1) return '';

        const languageCounts = {};
        jsonData.slice(1).forEach(item => {
            const language = item.Language;
            if (language) {
                languageCounts[language] = (languageCounts[language] || 0) + 1;
            }
        });

        const mostCommonLanguage = Object.keys(languageCounts).reduce((a, b) => languageCounts[a] > languageCounts[b] ? a : b);

        return mostCommonLanguage || 'Unknown';
    };

    // 
    const getColorByEmotion = (emotion) => {
        switch (emotion.toLowerCase()) {
            case 'angry':
                return 'red';
            case 'happy':
                return 'orange';
            case 'sad':
                return 'blue';
            case 'neutral':
                return 'gray';
            default:
                return 'black';
        }
    };

    // 
    const getEnglishEmotion = (emotion) => {
        const englishEmotion = emotion.split('/').pop();
        return englishEmotion.trim();
    };


    return (
        <ThemeProvider theme={theme}>
            <Container>
                <Typography variant="h4" component="h2" gutterBottom align="center" sx={{ marginTop: 4 }}>
                    Tone-base Speech Emotion Recognition Web Application
                </Typography>
                

                <Box display="flex" justifyContent="space-around" alignItems="center" sx={{ marginTop: 4, gap: 2 }}>
                    <Button variant="contained" component="label">
                        Upload Audio
                        <input type="file" accept=".mp3, .wav, .webm" onChange={handleFileChange} hidden />
                    </Button>
                    <Button variant="contained" color={isRecording ? "error" : "secondary"} onClick={handleRecord}>
                        {isRecording ? "Stop Recording" : "Record Audio"}
                    </Button>
                </Box>

                <Box display="flex" justifyContent="center" sx={{ marginTop: 4 }}>
                    {audioUrl ? <audio controls src={audioUrl} style={{ width: '100%' }} /> : <div style={{ height: '54px', width: '100%' }}></div>}
                </Box>

                <Box display="flex" justifyContent="center" sx={{ marginTop: 2 }}>
                    <Button variant="contained" onClick={handleRecognize} disabled={isRecording}>
                        Recognize
                    </Button>
                </Box>

                <Box sx={{ display: 'flex', justifyContent: 'space-between', marginTop: 4 }}>
                    {/*  */}
                    {jsonData ? (
                        jsonData.length === 1 ? (
                            <Typography variant="body2" color="text.secondary">
                                No one seems to be speaking on this audio.
                            </Typography>
                        ) : (
                            <Card sx={{ flex: 1, marginRight: 2 }}>
                                <CardContent>
                                    <Typography variant="h5" component="h2" gutterBottom>
                                        Overall Results
                                    </Typography>
                                    <Typography variant="body1">
                                        <strong>Overall Emotion: </strong>
                                        <span style={{ color: getColorByEmotion(getEnglishEmotion(jsonData[0]['Overall Emotion'])) }}>
                                            {getEnglishEmotion(jsonData[0]['Overall Emotion'])}
                                        </span>
                                    </Typography>
                                    <Typography variant="body1"><strong>Overall Language:</strong> {calculateOverallLanguage()}</Typography>

                                    <Typography variant="h5" component="h2" gutterBottom sx={{ marginTop: 2 }}>
                                        Detailed Results
                                    </Typography>
                                    {jsonData.slice(1).map((item, index) => (
                                        <Typography key={index} variant="body1" style={{ color: getColorByEmotion(getEnglishEmotion(item.Emotion)) }}>
                                            {item.Text} ({getEnglishEmotion(item.Emotion)})
                                        </Typography>
                                    ))}
                                </CardContent>
                            </Card>
                        )
                    ) : (
                        <Typography variant="body2" color="text.secondary">
                            No results to display.
                        </Typography>
                    )}
                    {/*  */}
                    <Box
                        sx={{
                            display: 'flex',
                            flexDirection: 'column',
                            alignItems: 'flex-start',
                            marginLeft: 2,
                            padding: '16px',
                            backgroundColor: '#f9f9f9',
                            borderRadius: '8px',
                            boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
                            flex: '0 0 auto',
                        }}
                    >
                        <Typography variant="h6" component="h3" gutterBottom>
                            Emotion Card
                        </Typography>
                        {[
                            { color: 'red', emotion: 'Angry' },
                            { color: 'orange', emotion: 'Happy' },
                            { color: 'blue', emotion: 'Sad' },
                            { color: 'gray', emotion: 'Neutral' },
                            { color: 'black', emotion: 'Unknown' },
                        ].map(({ color, emotion }) => (
                            <Box key={emotion} sx={{ display: 'flex', alignItems: 'center', marginBottom: '8px' }}>
                                <Box
                                    sx={{
                                        width: '16px',
                                        height: '16px',
                                        borderRadius: '50%',
                                        backgroundColor: color,
                                        marginRight: '8px',
                                    }}
                                />
                                <Typography variant="body1">{emotion}</Typography>
                            </Box>
                        ))}
                    </Box>
                </Box>

            </Container>
        </ThemeProvider>
    );

}

export default App;
